"use client";

import * as React from "react";
import dynamic from "next/dynamic";
import { DashboardLayout } from "@/components/dashboard/layout";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Switch } from "@/components/ui/switch";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import {
  GripVertical,
  CheckCircle2,
  X,
  Loader2,
  Edit3,
  ChevronDown,
  ChevronUp,
  Trash2,
} from "lucide-react";
import { useParams } from "next/navigation";
import axiosClient from "@/lib/axiosClient";

import {
  DndContext,
  closestCenter,
  PointerSensor,
  useSensor,
  useSensors,
} from "@dnd-kit/core";

import {
  arrayMove,
  SortableContext,
  verticalListSortingStrategy,
  useSortable,
} from "@dnd-kit/sortable";

import { CSS } from "@dnd-kit/utilities";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Collapsible, CollapsibleContent, CollapsibleTrigger } from "@/components/ui/collapsible";

// Client-only preview
const Perview = dynamic(() => import("@/app/preview/page"), { ssr: false });

/* ----------------------------------------------------
   TOAST
---------------------------------------------------- */
const Toast = ({ message, type = "success", onClose }) => {
  React.useEffect(() => {
    const timer = setTimeout(onClose, 4000);
    return () => clearTimeout(timer);
  }, [onClose]);

  return (
    <div
      className={`fixed bottom-8 left-1/2 -translate-x-1/2 z-50 px-6 py-4 rounded-lg shadow-2xl text-white font-medium flex items-center gap-3 ${
        type === "success" ? "bg-green-600" : "bg-red-600"
      }`}
    >
      {type === "success" ? <CheckCircle2 className="h-5 w-5" /> : <X className="h-5 w-5" />}
      {message}
    </div>
  );
};

/* ----------------------------------------------------
   SORTABLE ITEM WITH EDITOR
---------------------------------------------------- */
function SortableSectionItem({
  item,
  availableSections,
  onToggle,
  onRemove,
  onContentChange,
}) {
  const { attributes, listeners, setNodeRef, transform, transition } =
    useSortable({ id: item.section });

  const [mounted, setMounted] = React.useState(false);
  const [isOpen, setIsOpen] = React.useState(false);

  React.useEffect(() => setMounted(true), []);

  const style = {
    transform: mounted && transform ? CSS.Transform.toString(transform) : undefined,
    transition: mounted ? transition : undefined,
  };

  const sectionDef = availableSections.find((s) => s.section === item.section);
  const label = sectionDef?.label || item.section;

  const handleFieldChange = (fieldKey, value) => {
    onContentChange(item.section, { ...item.content, [fieldKey]: value });
  };

  return (
    <div ref={setNodeRef} style={style} className="rounded-lg border bg-white shadow-sm">
      {/* Header */}
      <div className="flex items-center justify-between p-4 border-b">
        <div className="flex items-center gap-3">
          <GripVertical
            className="h-5 w-5 cursor-grab text-gray-400"
            {...attributes}
            {...listeners}
          />
          <span className="font-medium">{label}</span>
        </div>

        <div className="flex items-center gap-3">
          <Button
            variant="ghost"
            size="icon"
            onClick={() => setIsOpen(!isOpen)}
          >
            {isOpen ? <ChevronUp className="h-4 w-4" /> : <ChevronDown className="h-4 w-4" />}
          </Button>
          <Switch checked={item.enabled} onCheckedChange={() => onToggle(item.section)} />
          <Button size="sm" variant="ghost" onClick={() => onRemove(item.section)}>
            <Trash2 className="h-4 w-4 text-red-600" />
          </Button>
        </div>
      </div>

      {/* Collapsible Content Editor */}
      <Collapsible open={isOpen}>
        <CollapsibleContent className="p-4 space-y-4">
          {sectionDef?.schema?.fields?.length > 0 ? (
            sectionDef.schema.fields.map((field) => (
              <div key={field.key} className="space-y-2">
                <Label htmlFor={field.key}>
                  {field.label} {field.required && <span className="text-red-500">*</span>}
                </Label>

                {field.type === "text" && (
                  <Input
                    id={field.key}
                    value={item.content[field.key] || ""}
                    onChange={(e) => handleFieldChange(field.key, e.target.value)}
                    placeholder={field.label}
                  />
                )}

                {field.type === "textarea" && (
                  <Textarea
                    id={field.key}
                    value={item.content[field.key] || ""}
                    onChange={(e) => handleFieldChange(field.key, e.target.value)}
                    placeholder={field.label}
                    rows={4}
                  />
                )}

                {field.type === "image" && (
                  <div className="space-y-2">
                    <Input
                      id={field.key}
                      value={item.content[field.key] || ""}
                      onChange={(e) => handleFieldChange(field.key, e.target.value)}
                      placeholder="https://example.com/image.jpg"
                    />
                    {item.content[field.key] && (
                      <img
                        src={item.content[field.key]}
                        alt="Preview"
                        className="w-full h-48 object-cover rounded-lg border"
                        onError={(e) => (e.currentTarget.src = "/placeholder.svg")}
                      />
                    )}
                  </div>
                )}
              </div>
            ))
          ) : (
            <p className="text-sm text-muted-foreground">No editable fields defined.</p>
          )}
        </CollapsibleContent>
      </Collapsible>
    </div>
  );
}

/* ----------------------------------------------------
   MAIN BUILDER
---------------------------------------------------- */
export default function LandingPageBuilder() {
  const params = useParams();
  const productId = params?.productId ? Number(params.productId) : null;

  const [selectValue, setSelectValue] = React.useState("");
  const [isSaving, setIsSaving] = React.useState(false);
  const [isLoading, setIsLoading] = React.useState(true);
  const [toast, setToast] = React.useState(null);

  // All global sections from API
  const [availableSections, setAvailableSections] = React.useState([]);

  // Active sections with order, enabled, and content
  const [activeSections, setActiveSections] = React.useState([]);

  const [styles] = React.useState({
    primaryColor: "#ecedf4ff",
    backgroundColor: "#000000",
    textColor: "#a97d38",
    headlineSize: 28,
    paragraphSize: 16,
  });

  const showToast = (message, type = "success") => setToast({ message, type });

  const sensors = useSensors(useSensor(PointerSensor, { activationConstraint: { distance: 6 } }));

  /* ----------------------------------------------------
     FETCH DATA
  ---------------------------------------------------- */
  React.useEffect(() => {
    const fetchData = async () => {
      try {
        setIsLoading(true);

        let active = [];
        let globalSections = [];

        if (productId) {
          // Product-specific
          const res = await axiosClient.get(`/products/${productId}/landing-page`);
          if (res.data.success && res.data.data?.sections?.length > 0) {
            const sections = res.data.data.sections;
            sections.sort((a, b) => a.sort_order - b.sort_order);

            active = sections.map((sec) => ({
              section: sec.global_section.key,
              enabled: sec.is_published,
              content: sec.content || {},
            }));

            globalSections = sections.map((sec) => ({
              section: sec.global_section.key,
              label: sec.global_section.name,
              schema: sec.global_section.schema,
            }));
          }
        }

        // Always fetch full global sections list (for adding new ones)
        const globalRes = await axiosClient.get("/landing-page/sections");
        if (globalRes.data.success && globalRes.data.data?.sections?.length > 0) {
          const sections = globalRes.data.data.sections
            .filter((s) => s.is_active)
            .sort((a, b) => a.sort_order - b.sort_order);

          globalSections = sections.map((sec) => ({
            section: sec.key,
            label: sec.name,
            schema: sec.schema,
          }));

          // If no product-specific active sections, use global defaults (optional)
          if (!productId && active.length === 0) {
            active = sections.map((sec) => ({
              section: sec.key,
              enabled: true,
              content: {}, // start empty
            }));
          }
        }

        setAvailableSections(globalSections);
        setActiveSections(active);
      } catch (error) {
        console.error(error);
        showToast("Failed to load sections", "error");
      } finally {
        setIsLoading(false);
      }
    };

    fetchData();
  }, [productId]);

  /* ----------------------------------------------------
     SECTION MANAGEMENT
  ---------------------------------------------------- */
  const handleAddSection = (sectionKey) => {
    if (!sectionKey || activeSections.some((s) => s.section === sectionKey)) return;

    setActiveSections((prev) => [
      ...prev,
      { section: sectionKey, enabled: true, content: {} },
    ]);
    setSelectValue("");
  };

  const toggleEnabled = (key) =>
    setActiveSections((prev) =>
      prev.map((s) => (s.section === key ? { ...s, enabled: !s.enabled } : s))
    );

  const removeSection = (key) =>
    setActiveSections((prev) => prev.filter((s) => s.section !== key));

  const updateContent = (sectionKey, newContent) =>
    setActiveSections((prev) =>
      prev.map((s) => (s.section === sectionKey ? { ...s, content: newContent } : s))
    );

  const onDragEnd = ({ active, over }) => {
    if (!over || active.id === over.id) return;
    setActiveSections((prev) => {
      const oldIndex = prev.findIndex((s) => s.section === active.id);
      const newIndex = prev.findIndex((s) => s.section === over.id);
      return arrayMove(prev, oldIndex, newIndex);
    });
  };

  /* ----------------------------------------------------
     SAVE
  ---------------------------------------------------- */
  const handleSave = async () => {
    setIsSaving(true);
    try {
      const payload = activeSections.map((sec, index) => ({
        global_landing_page_section_key: sec.section,
        content: sec.content || {},
        is_published: sec.enabled,
        sort_order: index + 1,
      }));

      const res = productId ? await axiosClient.put(`/products/${productId}/landing-page`, { sections: payload }) : await axiosClient.put("/landing-page/sections", { sections: payload });

      if (res.data.success) {
        showToast("Saved successfully!");
      } else {
        showToast(res.data.message || "Save failed", "error");
      }
    } catch (error) {
      showToast("Save failed", "error");
    } finally {
      setIsSaving(false);
    }
  };

  const previewPayload = {
    templateName: "modern",
    sections: activeSections
      .filter((s) => s.enabled)
      .map((s) => ({ section: s.section, content: s.content })),
    styles,
  };

  const availableToAdd = availableSections.filter(
    (s) => !activeSections.some((a) => a.section === s.section)
  );

  const pageTitle = productId
    ? `Product #${productId} Landing Page Builder`
    : "Global Landing Page Builder";

  if (isLoading) {
    return (
      <DashboardLayout>
        <div className="flex items-center justify-center h-screen">
          <Loader2 className="h-12 w-12 animate-spin text-primary" />
          <p className="ml-4 text-lg">Loading sections...</p>
        </div>
      </DashboardLayout>
    );
  }

  return (
    <>
      <DashboardLayout>
        <div className="max-w-7xl mx-auto space-y-8 p-6">
          <div className="flex justify-between items-center">
            <h1 className="text-3xl font-bold">{pageTitle}</h1>
            <Button onClick={handleSave} disabled={isSaving} size="lg">
              {isSaving ? "Saving..." : "Save Changes"}
            </Button>
          </div>

          <div className="grid lg:grid-cols-2 gap-8">
            {/* Controls */}
            <div className="space-y-6">
              <Card>
                <CardHeader>
                  <CardTitle>Add Section</CardTitle>
                </CardHeader>
                <CardContent>
                  <Select value={selectValue} onValueChange={handleAddSection}>
                    <SelectTrigger>
                      <SelectValue placeholder="Choose a section..." />
                    </SelectTrigger>
                    <SelectContent>
                      {availableToAdd.length > 0 ? (
                        availableToAdd.map((s) => (
                          <SelectItem key={s.section} value={s.section}>
                            {s.label}
                          </SelectItem>
                        ))
                      ) : (
                        <p className="p-4 text-sm text-center text-muted-foreground">
                          All sections added
                        </p>
                      )}
                    </SelectContent>
                  </Select>
                </CardContent>
              </Card>

              <Card>
                <CardHeader>
                  <CardTitle>Sections</CardTitle>
                  <CardDescription>Drag to reorder • Click ↓ to edit content</CardDescription>
                </CardHeader>
                <CardContent>
                  {activeSections.length > 0 ? (
                    <DndContext sensors={sensors} collisionDetection={closestCenter} onDragEnd={onDragEnd}>
                      <SortableContext items={activeSections.map((s) => s.section)} strategy={verticalListSortingStrategy}>
                        <div className="space-y-4">
                          {activeSections.map((item) => (
                            <SortableSectionItem
                              key={item.section}
                              item={item}
                              availableSections={availableSections}
                              onToggle={toggleEnabled}
                              onRemove={removeSection}
                              onContentChange={updateContent}
                            />
                          ))}
                        </div>
                      </SortableContext>
                    </DndContext>
                  ) : (
                    <p className="text-center py-8 text-muted-foreground">
                      No sections yet. Add one above.
                    </p>
                  )}
                </CardContent>
              </Card>
            </div>

            {/* Preview */}
            <Card>
              <CardHeader>
                <CardTitle>Mobile Preview</CardTitle>
              </CardHeader>
              <CardContent className="flex justify-center py-8">
                <div className="w-[360px] h-[720px] rounded-[45px] border-[14px] border-black bg-black overflow-hidden relative shadow-2xl">
                  <div className="absolute top-0 left-1/2 -translate-x-1/2 w-40 h-6 bg-black rounded-b-3xl z-20" />
                  <div className="bg-white w-full h-full overflow-y-auto">
                    <Perview data={previewPayload} />
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>

          {toast && <Toast message={toast.message} type={toast.type} onClose={() => setToast(null)} />}
        </div>
      </DashboardLayout>
    </>
  );
}